  margin-left: 10px;
  font-size: 32px;
  font-weight: 500;
  line-height: 38px;
}

.header__container {
  background: url("background320w.279270ed.png") center / contain no-repeat;
  border-radius: 10px;
  flex-direction: column;
  width: 100vw;
  max-width: 280px;
  height: 460px;
  margin: 20px auto;
  padding: 14px 14px 20px;
  display: flex;
}

.header__container--bar {
  width: 90%;
  margin-left: 5%;
  padding-bottom: 312px;
}

.header__container__logo {
  flex-flow: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__container__logo--link {
  color: #fff;
  fill: currentColor;
  flex-flow: row;
  gap: 8px;
  display: flex;
}

.header__container__logo--link--icon {
  animation: 2s infinite filmSvg;
  display: flex;
  position: relative;
  overflow: hidden;
  transform: rotateY(40deg);
}

.header__container__logo--link--icon-svg {
  width: 50px;
  height: 80px;
}

.header__navigation__menu {
  flex-flow: row;
  gap: 4px;
  padding-bottom: 8px;
  display: flex;
}

.header__navigation__menu--item {
  text-align: center;
  text-transform: uppercase;
  background-color: #fff;
  border-radius: 100px;
  align-items: center;
  padding: 10px 26px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  transition: box-shadow .3s ease-in-out;
  display: flex;
  position: relative;
  overflow: hidden;
}

.header__navigation__menu--item:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
}

.header__navigation__menu--item:hover {
  cursor: pointer;
  transform: scale(.9);
  box-shadow: 0 2px 4px #0000004d;
}

.header__navigation__menu--item:hover:before {
  transform: scale(1);
}

.header__navigation__menu--item-link {
  color: #000;
  transition: transform .3s ease-in-out;
}

.header__navigation__menu--item.header__navigation__menu--selected {
  background-color: #000;
}

.header__navigation__menu--item.header__navigation__menu--selected a {
  color: #fff;
}

.header__search {
  position: relative;
}

.header__search input {
  background-color: #fff;
  border: none;
  border-radius: 100px;
  width: 100%;
  height: 34px;
  margin: 0 auto;
  padding: 10px 0 10px 14px;
  transition: border-color .5s ease-out;
}

.header__search input:focus {
  color: #902221;
  border: 2px solid red;
  outline: none;
}

.header__search button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  margin: 0 auto;
  padding: 0;
  position: absolute;
  bottom: 0;
  right: 14px;
}

.header__search button:hover .header__search__container--icon {
  width: 20px;
  height: 20px;
}

.header__search__container {
  position: relative;
}

.header__search__container--icon {
  fill: #fff;
  stroke: #000;
  width: 18px;
  height: 18px;
  position: absolute;
  top: -16px;
  left: -15px;
}

.header__search__container--error {
  color: #fff;
  align-items: center;
  width: 180px;
  padding: 8px 0 8px 15px;
  font-size: 10px;
  line-height: 12px;
  display: flex;
  position: absolute;
  top: 100%;
  left: 0;
}

